html, body {
  background-color: #0d0d0d;
  color: #f5f5f5;
  margin: 0;
  padding: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.book-search {
  max-width: 900px;
  margin: 2rem auto;
  padding: 2rem;
  background-color: #1a1a1a;
  border-radius: 12px;
  box-shadow: 0 4px 20px rgba(255, 165, 0, 0.05);
  color: #f5f5f5;
}

h1 {
  color: #ffa500;
  margin-bottom: 1.5rem;
}

form {
  display: flex;
  justify-content: center;
  gap: 1rem;
  flex-wrap: wrap;
  margin-bottom: 1.5rem;
}

input[type="text"] {
  padding: 0.75rem;
  width: 300px;
  border: 1px solid #555;
  border-radius: 8px;
  background-color: #2c2c2c;
  color: #f5f5f5;
  font-size: 1rem;
  transition: border-color 0.2s;
}

input[type="text"]:focus {
  border-color: #ffa500;
  outline: none;
}

button {
  padding: 0.75rem 1.5rem;
  background-color: #ffa500;
  color: black;
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

button:hover {
  background-color: #e69500;
}

.upload {
  margin-top: 1rem;
  font-size: 0.9rem;
  color: #ccc;
}

.upload input[type="file"] {
  margin-top: 0.5rem;
  color: #ddd;
}

.book-list {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  justify-content: center;
  margin-top: 2rem;
}

.book-item {
  background-color: #2a2a2a;
  padding: 1rem;
  width: 200px;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(255, 165, 0, 0.05);
  text-align: center;
}

.book-item img {
  width: 100%;
  height: auto;
  border-radius: 4px;
  margin-bottom: 0.5rem;
}

.book-item h3 {
  font-size: 1rem;
  color: #ffa500;
  margin: 0.5rem 0;
}

.book-item p {
  font-size: 0.9rem;
  color: #bbb;
}

.pagination {
  margin-top: 2rem;
  display: flex;
  justify-content: center;
  gap: 1rem;
}

.pagination button {
  padding: 0.6rem 1.2rem;
  background-color: #333;
  color: #ffa500;
  border: none;
  border-radius: 6px;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.pagination button:hover {
  background-color: #555;
}

.pagination button:disabled {
  opacity: 0.4;
  cursor: not-allowed;
}
